.tooltip {
    position: absolute;
    text-align: center;
    width: 120px;
    height: 28px;
    padding: 2px;
    font: 12px sans-serif;
    background: lightsteelblue;
    border: 0px;
    border-radius: 8px;
    pointer-events: none;
}

#world-map {
    width: 800px;
    height: 400px;
}

.map-container {
    position: relative;
    width: 800px;
    height: 400px;
}

.zoom-controls {
    position: absolute;
    bottom: 10px;
    right: 10px;
    display: flex;
    flex-direction: column;
}

.zoom-button {
    background-color: #acacacd7;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-size: 18px;
    margin: 5px 0;
    border-radius: 4px;
}

.zoom-button:hover {
    background-color: #e0e0e0;
}

.color-bar {
    position: absolute;
    left: -15px;
    top: 30px;
}