body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh; /* 确保 body 元素至少有视口高度 */
  margin: 0; /* 移除默认的外边距 */
}

.main_content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


.world_map {
    display: grid;
    place-items: center;
    height: 100vh; /* or any other value */
  }